<template>
    <div id="container">
        <div class="city-daqiang-c">
            <p class="city-daqiang-pos">当前位置</p>
            <p class="city-daqiang-city" v-text="currentCityNameFn()"></p>
        </div>
        <div class="city-daqiang-hot">
            热门城市
        </div>
        <ul class="clear city-daqiang-hot-lis">
            <li class='fl'
                v-for="item in backgroundData.hotCitys"
                v-text="item.cityName"
                v-on:click="selectCityFn(item.cityName)">
            </li>
        </ul>
        <div class="city-more-c">
            <div class="city-more-btn" v-on:click="cityAllFlag = !cityAllFlag">
                <div class="city-more-btn-con">
                    <span class="city-more-words">展开更多城市</span>
                    <span :class="{'city-more-yc': cityAllFlag, 'city-more-xs': !cityAllFlag}"></span>
                </div>
            </div>

            <div class="city-all-zongg" v-show="cityAllFlag">
                <div class="city-all-c" v-for="item in backgroundData.moreCitys">
                    <p class="city-all-szm" v-text="item.cityAbbr"></p>
                    <ul v-for="items in item.cityList">
                        <li v-text="items.cityName" v-on:click="selectCityFn(items.cityName)"></li>
                    </ul>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import Rou from '@src/router/index'
export default {
    name: 'city',
    data () {
        return {
            "backgroundDataApiUrl":  'http://10.5.17.187/citylist',
            // "backgroundDataSelectApiUrl":  'http://10.5.17.187/choosecity',
            "parameterItem": {},
            "backgroundData": '',
            "cityAllFlag": false
        }
    },
    beforeRouteEnter: function (to, from, next) {
        next(function (vm) {
            var _this = vm;
            _this.$http.post(_this.backgroundDataApiUrl)
                        .then(function (res) {
                            // console.log(res.body.data);
                            _this.backgroundData = res.body.data;
                        })
                        .catch(function(res) {
                            // console.log('出错了');
                        });
        });
    },
    methods: {
        //选择城市后，跳回之前页面
        selectCityFn: function (cityName) {
            console.log(cityName);
            setCookie('cityName', encodeURIComponent(cityName));
            Rou.replace({path: Rou.thisPagePath});
        },
        //cookie中获取当前城市名称
        currentCityNameFn: function () {
            return decodeURIComponent(getCookie('cityName'));
        }
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



