<template>
	<div id="container">
        <div class="bigPic">
            <img v-bind:src="backgroundData.brandPic" alt="">
        </div>
        <ul>
            <li class="brand-container clear" v-for="item in backgroundData.goodsList">
	            <router-link :to="{name: 'goodsinfo', query: {goodsId: item.goodsId}}">
	                <div class="brand-pic fl">
	                    <img v-bind:src="item.goodsPic" alt="">
	                </div>
	                <div class="brand-lis fr" >
	                    <h3 v-text='item.goodsName'></h3>
	                    <p>¥1000</p>
	                    <span>已售&nbsp;1391</span>
	                </div>
	            </router-link>
            </li>
        </ul>
    </div>
</template>

<script>
	export default {
		name: 'brandpage',
			data () {
			return {
				"backgroundDataApiUrl":  'http://10.5.17.187/brandpage',	//brandId=5
				"parameterItem": {
					"brandId": this.$route.params.brandId
				},
				"backgroundData": '',
			}
		},
		beforeRouteEnter: function (to, from, next) {
	        next(function (vm) {
	            var _this = vm;
	            _this.$http.post(_this.backgroundDataApiUrl, _this.parameterItem, {emulateJSON: true})
	                        .then(function (res) {
	                            // console.log(res.body.data);
	                            _this.backgroundData = res.body.data;
	                        })
	                        .catch(function(res) {
	                            // console.log('出错了');
	                        });
	        });
	    },
		methods: {

		}
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>


