import Vue from 'vue'
import Router from 'vue-router'
import Resource from 'vue-resource'
import Index from '@components/index/index'
import City from '@components/city/city'
import Goodsinfo from '@components/card/goodsinfo'
import Brandpage from '@components/card/brandpage'
import Commentlistpage from '@components/card/commentlistpage'
import Submitorderpage from '@components/order/submitorderpage'
import Loading from '@src/components/loading/loading'

Vue.use(Router)
Vue.use(Resource)


const router = new Router({
    routes: [
        {
            path: '/index/index',       //首页
            name: 'index',
            component: Index
        },
        {
            path: '/city/city',     //城市
            name: 'city',
            component: City
        },
        {
            path: '/card/goodsinfo/:goodsId',        //商品详情
            name: 'goodsinfo',
            component: Goodsinfo
        },
        {
            path: '/card/brandpage/:brandId',        //品牌页
            name: 'brandpage',
            component: Brandpage
        },
        {
            path: '/card/commentlistpage',        //总评价
            name: 'commentlistpage',
            component: Commentlistpage
        },
        {
            path: '/order/submitorderpage',     //提交订单
            name: 'submitorderpage',
            component: Submitorderpage
        },
        {
            path: '/loading/loading',     //提交订单
            name: 'loading',
            component: Loading
        },
        {
            path: '/*',     //重定向
            redirect: '/index/index'
        }
    ]
})
router.thisPagePath = '/index/index';
// console.log(router.thisPagePath);
router.beforeEach((to, from, next) => {
    const cityName = decodeURIComponent(getCookie('cityName'));
    // console.log(cityName);
    // console.log('之前页面的名字是'+from.name);    //从哪里来
    // console.log(to.name == 'index');   //当前
    if (to.name == 'city') {
        next();
        return;
    } else if (!cityName) {
        // console.log(1111);
        // console.log(to.fullPath);
        // console.log(1111);
        router.thisPagePath = to.fullPath;
        console.log('城市不存在');
        next({name: 'city'});
        return;
    } else {
        next();
    }
})


export default router;



