<template>
	<div id="container">
		<div class="swiper-container swiper-container-title">
            <div class="swiper-wrapper">
                <div v-for="item in backgroundData.turnPic" class="swiper-slide">
                    <a v-bind:href="item.goUrl">
                        <img v-bind:src="item.pic" alt="">
                    </a>
                </div>
            </div>
            <div class="swiper-pagination"></div>
        </div>

        <div class="city-container">
            <router-link :to="{name: 'city'}">
            	<div class="city clear">
	                <div class="building-container fl">
	                    <img src="/static/images/building.png" alt="">
	                </div>
	                <div class="choose-city fl" v-text="currentCityNameFn()">
	                    选择城市
	                </div>
	                <div class="landmark-container fr">
	                    <img src="/static/images/landmark.png" alt="">
	                </div>
	            </div>
            </router-link>
        </div>

        <div class="kind-container" v-for="item in backgroundData.salesList">
            <div class="swiper-container swiper-containers">
                <div class="swiper-wrapper">
                    <div class="swiper-slide ">
                    	<router-link :to="{path: '/card/brandpage/'+item.brandId}">
                            <img class='kind-pic' v-bind:src="item.pic" alt="">
                        </router-link>
                        <p class="kind-name" v-text='item.brandName'>资和信...</p>
                    </div>
                    <div v-for="items in item.goodsList" class="swiper-slide">
                    	<router-link :to="{path: '/card/goodsinfo/'+items.goodsId}">
                        	<img class='kind-pic' v-bind:src="items.pic" alt="">
                        </router-link>
                        <p class="kind-name" v-text='items.goodsName'>资和信</p>
                        <div class="kind-shop-price">
                            <span v-text='items.salePrice'>¥1000.00</span>
                            <span v-if="items.salePrice != items.oldPrice" v-text='items.oldPrice'>1000.00</span>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
	name: 'index',
	data () {
		return {
			"backgroundDataApiUrl": 'http://10.5.17.187/defaulpage',
			"backgroundData": '',
		}
	},
	beforeRouteEnter: function (to, from, next) {
		next(function (vm) {
			var _this = vm;
			_this.$http.post(_this.backgroundDataApiUrl)
					.then(function (res) {
						_this.backgroundData = res.body.data;
					})
					.catch(function(res) {
						// console.log('出错了');
	                });

		});
	},
	updated: function(){
        new Swiper('.swiper-container-title', {
            autoplay: 1000,//可选选项，自动滑动
            pagination : '.swiper-pagination',
            loop: true,
            autoplayDisableOnInteraction : false,
        })
        new Swiper('.swiper-containers', {
            slidesPerView: 3,
        })
    },
	methods: {
		//cookie中获取当前城市名称
        currentCityNameFn: function () {
            return decodeURIComponent(getCookie('cityName'));
        }
	}
}


</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



