<template>
	<ul class="stars-group clear">
        <li class="fl" v-for="starClass in starClasses">
            <i :class="starClass"></i>
        </li>
    </ul>
</template>

<script>
	const STARTOTAL = 5;	//评价共有5颗星
	export default {
		name: 'star',
		data: function () {
			return {
				"starTotal": STARTOTAL,
			}
		},
		props: ['score'],
		computed: {
			starClasses: function () {
				let _this = this;
				let arr = [];
				let str = '';
				for (var i = 1; i <= _this.starTotal; i++) {
					str = (i <= _this.score) ? 'star-yellow' : 'star-gray';
					arr.push(str);
				}
				return arr;
			}
		}
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>


