<template>
	<div class="container">
        <div class="details-t">
            <figure>
                <img :src="backgroundData.picSrc" alt="图片不能正常显示">
            </figure>
        </div>
        <div class="details-introduce">
            <h4 class="details-introduce-t" v-text="backgroundData.goodsName"></h4>
            <div class="details-introduce-b clear">
                <div class="details-introduce-b-price fl">
                    <span v-text="'¥'+backgroundData.salePrice"></span>
                    <span v-if="backgroundData.salePrice != backgroundData.oldPrice" v-text="backgroundData.oldPrice"></span>
                </div>
                <div class="details-introduce-b-sold flex flex-center fr">
                    <i class="sold"></i>
                    <span v-text="'已售 ' + backgroundData.saleCounts"></span>
                </div>
            </div>
        </div>
        <div class="details-explain" v-text="backgroundData.description"></div>
        <div class="space20"></div>
        <div class="details-count-down flex flex-center">
            <i class="djs"></i>
            <span v-text="countDownTimeText"></span>
        </div>
        <div class="space20"></div>

        <div class="all-comments">
            <div class="all-comments-t flex flex-center flex-content">
                <div class="all-comments-t-l flex flex-center">
                    <v-star :score="score"></v-star>
                    <span v-text="score + '分'"></span>
                </div>
                <router-link :to="{name: 'commentlistpage', params: {
                                                                        goodsId: backgroundData.goodsId,
                                                                        page: page,
                                                                        pageSize: pageSize
                                                                    }
                                    }">
                    <div class="all-comments-t-r flex flex-center">
                        <span v-text="myEvaluate.userCounts+'条评价'"></span>
                        <i class="right-arrow-yellow"></i>
                    </div>
                </router-link>
            </div>
            <v-comment :scoreList="myEvaluate.scoreList"></v-comment>
        </div>


        <footer class="details-footer flex flex-center flex-content">
            <ul class="details-limited clear">
                <li class="fl" v-on:click="selectedNumberReduceFn">
                    <i class="jian1"></i>
                </li>
                <li class="fl" v-text="selectedNumber"></li>
                <li class="fl" v-on:click="selectedNumberGagaFn">
                    <i class="jia1"></i>
                </li>
            </ul>
            <div class="details-footer-r flex flex-center">
                <div class="details-total">
                    合计：<b v-text="(backgroundData.salePrice * selectedNumber).toFixed(2)">¥1000.00</b>
                </div>
                <router-link :to="{name: 'submitorderpage', params: {
                                                                goodsId: backgroundData.goodsId,
                                                                goodsName: backgroundData.goodsName
                                                            }
                                }">
                	<button class="details-purchase-btn">立即购买</button>
                </router-link>
            </div>
        </footer>
    </div>
</template>

<script>
    import Star from '@src/components/common/star'
    import Comment from '@src/components/common/comment'
    export default {
        name: 'goodsinfo',
        data () {
            return {
                "backgroundDataApiUrl": 'http://10.5.17.187/goodsinfo',     //goodsId=100000646
                "parameterItem": {
                    "goodsId": this.$route.params.goodsId
                },
                "backgroundData": '',
                "myEvaluate": '',       //评价
                "selectedNumber": "2",      //已选数量
                "score": "0",      //总分数
                "countDownTimeText": '',     //倒计时内容
                "page": '',
                "pageSize": '',
            }
        },
        components: {
            'v-star': Star,
            'v-comment': Comment
        },
        beforeRouteEnter: function (to, from, next) {
            next(function (vm) {
                var _this = vm;
                _this.$http.post(_this.backgroundDataApiUrl, _this.parameterItem, {emulateJSON: true})
                            .then(function (res) {
                                _this.backgroundData = res.body.data;
                                _this.myEvaluate = res.body.data.commentList;
                                _this.score = res.body.data.commentList.score;
                                _this.page = res.body.data.commentList.page;
                                _this.pageSize = res.body.data.commentList.pageSize;
                                _this.getCountDownFn(_this.backgroundData.endTime);
                            })
                            .catch(function(res) {
                                alert('出错了');
                            });
            });
        },
        mounted: function () {
            var _this = this;
            setInterval(function () {
                _this.getCountDownFn(_this.backgroundData.endTime)
            }, 1000)
        },
        methods: {
            //倒计时
            getCountDownFn: function (endTime) {
                if (!endTime) {
                    return;
                }
                endTime=endTime.replace(/-/ig,'/'); //兼容ios，不能用"-"
                var EndTime= new Date(endTime);
                var NowTime = new Date();
                var t =EndTime.getTime() - NowTime.getTime();
                var d, h, m, s=0;
                if(t>=0){
                  d=Math.floor(t/1000/60/60/24);
                  h=Math.floor(t/1000/60/60%24);
                  m=Math.floor(t /1000/60%60);
                  s=Math.floor(t/1000%60);
                }
                this.countDownTimeText = '剩余'+d+'天'+h+'小时'+m+'分'+s+'秒';
            },
            // 已选数量++
            selectedNumberGagaFn: function () {
                this.selectedNumber++;
                var surplusCount = parseFloat(this.backgroundData.surplusCount);
                surplusCount = (surplusCount >= 10) ? 10 : surplusCount;        //限制最大购买数量为10
                if (this.selectedNumber >= surplusCount) {
                    this.selectedNumber = surplusCount;
                    tishi2('已达最大购买数量');
                };
            },
            // 已选数量--
            selectedNumberReduceFn: function () {
                this.selectedNumber--;
                var surplusCount = parseFloat(this.backgroundData.surplusCount);
                if (this.selectedNumber <= 0) {
                    this.selectedNumber = 0;
                };
            }
        }
    };
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>


