<template>
	<h1>this is 总评价</h1>
</template>

<script>
	//goodsId,page,pageSize
	export default {
		name: 'commentlistpage',
			data () {
			return {
				"backgroundDataApiUrl":  'http://10.5.17.187/conmentlist',
				"parameterItem": {
                    "goodsId": this.$route.query.goodsId,
                    "page": this.$route.query.page,
                    "pageSize": this.$route.query.pageSize
                },
				"backgroundData": '',
			}
		},
		beforeRouteEnter: function (to, from, next) {
	        next(function (vm) {
	            var _this = vm;
	            _this.$http.post(_this.backgroundDataApiUrl)
	                        .then(function (res) {
	                            console.log(res.body.data);
	                            _this.backgroundData = res.body.data;
	                        })
	                        .catch(function(res) {
	                            alert('出错了');
	                        });
	        });
	    },
		methods: {

		}
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>


