<template>
	<h1>this is 创建订单</h1>
</template>

<script>
	export default {
		name: 'submitorderpage',
			data () {
			return {
				"backgroundDataApiUrl": 'http://10.5.17.187/createordernew',
				"parameterItem": {
                    "goodsId": this.$route.query.goodsId,
                    "goodsName": this.$route.query.goodsName
                },
				"backgroundData": '',
			}
		},
		beforeRouteEnter: function (to, from, next) {
			console.log(22222222222);
			next(function (vm) {
				var _this = vm;
				console.log(1111111);
				console.log(_this.parameterItem);
				console.log(_this.backgroundDataApiUrl);
	            _this.$http.post(_this.backgroundDataApiUrl, _this.parameterItem, {emulateJSON: true})
	                        .then(function (res) {
	                            console.log(res.body.data);
	                            _this.backgroundData = res.body.data;
	                        })
							.catch(function(res) {
								alert('出错了');
			                });

			});
		},
		methods: {

		}
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>


